body {
  margin: 0;
  overscroll-behavior: contain;
}
video,
#artist-stream {
  display: none;
}

@keyframes press-start-anim {
  from {
    filter: hue-rotate(0deg);
  }
  to {
    filter: hue-rotate(360deg);
  }
}
#press-start {
  font-family: monospace;
  font-size: 2rem;
  position: absolute;
  left: 50%;
  top: 50%;
  color: #fbbdf4;
  animation: press-start-anim 10s infinite;
  transition: opacity 0.3s;
  pointer-events: none;
}

.centered-cta {
  font-family: monospace;
  font-size: 2rem;
  position: absolute;
  left: 50%;
  top: 50%;
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: 42px;
  font-weight: bold;
}

.icon-cta {
  display: flex;
  cursor: pointer;

  svg {
    height: 60px;
    margin: 50px;
    width: 60px;
  }
}
.hue-changing {
  animation: press-start-anim 10s infinite;
}

#controls-container {
  position: absolute;
  font-family: monospace;
  font-weight: bold;
  font-size: 16px;
  user-select: none;
  left: 10px;
  top: 10px;

  div {
    margin: 10px;
    cursor: pointer;
  }
}

#volume {
  width: 300px;
  height: 20px;

  canvas {
    height: 100%;
    width: 100%;
  }
}
